@import url(https://fonts.googleapis.com/css?family=Montserrat:300,300i,400,400i,500,500i,600,600i,800,800i&display=swap);
@import url(https://cdn.web.wsu.edu/designsystem/1.x/wsu-icons/dist/wsu-icons.bundle.css);
html{font-family:"Montserrat",sans-serif;font-size:16px}a{color:#A60F2D}a:active{color:#CA1237}a:hover{color:#CA1237}a:visited{color:#A60F2D}h1,h2,h3,h4,h5,h6{-webkit-transition:font-size 300ms cubic-bezier(0, 0, 0.03, 1);-o-transition:font-size 300ms cubic-bezier(0, 0, 0.03, 1);transition:font-size 300ms cubic-bezier(0, 0, 0.03, 1)}p{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.wsu-u-visually-hidden{position:absolute;overflow:hidden;clip:rect(1px, 1px, 1px, 1px);width:1px;height:1px;margin:-1px;padding:0;-webkit-clip-path:inset(50%);clip-path:inset(50%)}.wsu-u-spacer{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;height:200px}.wsu-u-title{font-family:monospace;text-align:center}.wsu-c-icon-container{max-width:800px;margin:30px auto}.wsu-icon{display:inline-block;padding:20px;-webkit-transition:color 300ms cubic-bezier(0, 0, 0.03, 1);-o-transition:color 300ms cubic-bezier(0, 0, 0.03, 1);transition:color 300ms cubic-bezier(0, 0, 0.03, 1)}.wsu-icon:hover{color:#CA1237}

